import React, { useCallback } from 'react';
import Header from './Header/Header';
import Content from './Content/Content';

import { useAuth } from "../../context/auth";
import { UrlContext } from "../../context/url";

const Dashboard = (props) => {
    const { authTokens } = useAuth();    

    const getUrlPath = useCallback((resource) => {
        const protocol = "https";
        const ip = "passengertracer.net";
        const app = "apis/api/v1";
        const url = `${protocol}://${ip}/${app}/${resource}`;

        return url;
    }, [])

    return (
        <UrlContext.Provider value={{ getUrl: getUrlPath }}>
            <div className="dashboard">
                <Header pageTitle="LGU Module" user={authTokens.user} />
                <Content />
            </div>
        </UrlContext.Provider>
    )

}

export default Dashboard
